.button-container {
  position: absolute;
  bottom: 20px; /* Ajusta la distancia desde la parte inferior según tu preferencia */
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
}

.button-container a {
  display: inline-block;
  text-decoration: none;
}

.button-container button {
  background-color: #937e6082; /* Color dorado */
  color: #ffffffc2; /* Texto blanco para resaltar */
  border: none; /* Sin borde */
  border-radius: 50%; /* Hace que el botón sea redondo */
  padding: 15px 30px; /* Ajusta el espaciado según tus preferencias */
  font-size: 16px; /* Tamaño del texto */
  font-family: Georgia, serif; 
  cursor: pointer;
  box-shadow: 0 2px 4px #00000033; /* Sombra para destacar del fondo */
  transition: background-color 0.3s ease; /* Transición suave del color de fondo */
}

.button-container button:hover {
  background-color: #ff800017; /* Cambia el color de fondo al pasar el mouse */
}
